import { StatusBar } from "expo-status-bar";
import React, { useState } from "react";
import {
  View,
  Text,
  TouchableOpacity,
  TextInput,
} from "react-native";
import { Feather } from "@expo/vector-icons";
import Icon from "react-native-vector-icons/FontAwesome";
import { signUp } from "../services/auth";
import styles from "../styles/SignUpStyles";

export default function SignUp({ navigation }) {
  const [email, setEmail] = useState("");
  const [pw, setPw] = useState("");
  const [pwCheck, setPwCheck] = useState("");
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  
  //      
  const handleSignUp = () => {
    signUp(email, pw, pwCheck, name, phone, address, navigation);
  };

  return (
    <View style={styles.container}>
      <StatusBar style="auto" />

      <View style={styles.header}>
        <TouchableOpacity onPress={() => navigation.goBack()}>
          <Icon name="angle-left" size={34} color="white" />
        </TouchableOpacity>
        <Text style={styles.headerText}>Sign Up</Text>
      </View>

      <View style={styles.body}>
        {/*   */}
        <View style={styles.form}>
          <View style={styles.textContainer}>
            <Text></Text>
          </View>
          <TextInput
            placeholder=" ."
            onChangeText={setEmail}
            value={email}
            style={styles.input}
          />
          <TouchableOpacity style={styles.btn}>
            <Text> </Text>
          </TouchableOpacity>
        </View>
        {/*     */}
        <View style={styles.form}>
          <TextInput style={styles.input} />
          <View style={styles.textContainer}>
            <Text>4:39</Text>
          </View>
          <TouchableOpacity style={styles.btn}>
            <Text> </Text>
          </TouchableOpacity>
        </View>

        <View style={styles.hr} />

        {/*   */}
        <View style={styles.form}>
          <Text></Text>
          <TextInput
            placeholder=" ."
            secureTextEntry={true}
            onChangeText={setPw}
            value={pw}
            style={styles.input}
          />
        </View>
        {/*    */}
        <View style={styles.form}>
          <Text> </Text>
          <TextInput
            placeholder=" ."
            secureTextEntry={true}
            onChangeText={setPwCheck}
            value={pwCheck}
            style={styles.input}
          />
        </View>

        <View style={styles.hr} />

        {/*   */}
        <View style={styles.form}>
          <View style={styles.textContainer}>
            <Text></Text>
          </View>
          <TextInput
            placeholder=" ."
            onChangeText={setName}
            value={name}
            style={styles.input}
          />
          <TouchableOpacity style={styles.btn}>
            <Text> </Text>
          </TouchableOpacity>
        </View>

        {/*   */}
        <View style={styles.form}>
          <View style={styles.textContainer}>
            <Text></Text>
          </View>
          <TextInput
            placeholder="  ."
            onChangeText={setPhone}
            value={phone}
            style={styles.input}
          />
          <TouchableOpacity style={styles.btn}>
            <Text> </Text>
          </TouchableOpacity>
        </View>

        {/*   */}
        <View style={styles.form}>
          <View style={styles.textContainer}>
            <Text></Text>
          </View>
          <TextInput
            placeholder=" ."
            onChangeText={setAddress}
            value={address}
            style={styles.input}
          />
          <Feather name="search" size={24} color="#bbb" style={styles.icon} />
        </View>
      </View>

      <TouchableOpacity onPress={handleSignUp} style={styles.signUpBtn}>
        <Text></Text>
      </TouchableOpacity>
    </View>
  );
}